import type { Metadata } from 'next';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { ArrowLeft, Calendar, Clock, User, ArrowRight } from 'lucide-react';
import { Card, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import React from 'react';
import Navigation from '@/app/components/Navigation';
import { blogData } from '@/lib/blog-data';

// Dynamic Metadata based on the post
export async function generateMetadata({ params }: { params: Promise<{ slug: string }> }): Promise<Metadata> {
  const { slug } = await params;
  const post = blogData[slug];
  
  return {
    title: post ? `${post.title} | Mahbub's Blog` : 'Post Not Found',
    description: post?.title || 'Read our latest tech insights.',
  };
}

export function generateStaticParams() {
  return Object.keys(blogData).map((slug) => ({
    slug: slug,
  }));
}

export default async function BlogPostPage({ params }: { params: Promise<{ slug: string }> }) {
  const { slug } = await params;
  const post = blogData[slug];

  if (!post) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center dark:bg-gray-950 text-center px-4">
        <h1 className="text-3xl font-bold dark:text-white mb-4">Post Not Found</h1>
        <p className="text-gray-600 dark:text-gray-400 mb-8">The article you are looking for doesn't exist or has been moved.</p>
        <Button asChild className="rounded-full">
          <Link href="/blog">Back to All Blogs</Link>
        </Button>
      </div>
    );
  }

  return (
    <div className="flex flex-col min-h-screen bg-white dark:bg-gray-950 transition-colors duration-500">
      <Navigation />

      {/* Hero / Header Section */}
      <section className="pt-32 pb-12 bg-gray-50/50 dark:bg-gray-900/30">
        <div className="container mx-auto px-4">
          <div className="max-w-4xl mx-auto">
            <Button asChild variant="ghost" className="group mb-8 -ml-4 dark:text-gray-400 hover:bg-transparent">
              <Link href="/blog" className="flex items-center">
                <ArrowLeft className="w-4 h-4 mr-2 group-hover:-translate-x-1 transition-transform" />
                Back to Blog
              </Link>
            </Button>
            
            <div className="mb-6">
              <span className="text-xs font-bold px-4 py-1.5 bg-blue-600 text-white rounded-full uppercase tracking-widest">
                {post.category}
              </span>
            </div>

            <h1 className="text-4xl md:text-6xl font-extrabold text-gray-900 dark:text-white mb-8 tracking-tight leading-tight">
              {post.title}
            </h1>

            <div className="flex flex-wrap items-center gap-6 text-gray-500 dark:text-gray-400">
              <div className="flex items-center gap-2">
                <Calendar size={18} className="text-blue-600" />
                <span>{new Date(post.date).toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric' })}</span>
              </div>
              <div className="flex items-center gap-2">
                <Clock size={18} className="text-blue-600" />
                <span>{post.readTime}</span>
              </div>
              <div className="flex items-center gap-2">
                <User size={18} className="text-blue-600" />
                <span>By {post.author}</span>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Featured Image */}
      <section className="px-4 -mt-8">
        <div className="max-w-5xl mx-auto">
          <div className="aspect-[21/9] overflow-hidden rounded-3xl shadow-2xl border border-gray-100 dark:border-gray-800">
            <img 
              src={post.image} 
              alt={post.title} 
              className="w-full h-full object-cover hover:scale-105 transition-transform duration-700" 
            />
          </div>
        </div>
      </section>

      {/* Article Content */}
      <article className="py-16 px-4">
        <div className="max-w-3xl mx-auto">
          <div className="prose prose-lg dark:prose-invert max-w-none 
            prose-headings:text-gray-900 dark:prose-headings:text-white 
            prose-p:text-gray-700 dark:prose-p:text-gray-300 
            prose-strong:text-blue-600 dark:prose-strong:text-blue-400
            prose-h2:text-3xl prose-h2:mt-12 prose-h2:mb-6">
            {post.content}
          </div>

          {/* Contact CTA Block */}
          <div className="mt-20 p-10 rounded-3xl bg-blue-600 dark:bg-blue-600 text-white text-center shadow-2xl shadow-blue-500/20">
            <h3 className="text-3xl font-bold mb-4">Have a technical challenge?</h3>
            <p className="text-blue-50 mb-8 max-w-md mx-auto">
              I specialize in building high-performance web applications and AI integrations. Let's discuss your next project.
            </p>
            <Button asChild size="lg" variant="secondary" className="rounded-full px-10 font-bold hover:scale-105 transition-transform">
              <Link href="/#contact">Get in Touch</Link>
            </Button>
          </div>
        </div>
      </article>

      {/* Related Posts Section */}
      <section className="py-24 bg-gray-50 dark:bg-gray-900/50 border-t border-gray-100 dark:border-gray-800">
        <div className="container mx-auto px-4">
          <div className="max-w-5xl mx-auto">
            <div className="flex items-end justify-between mb-12">
              <div>
                <h2 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">Recommended Reading</h2>
                <p className="text-gray-600 dark:text-gray-400">Expand your knowledge with these related articles.</p>
              </div>
              <Button asChild variant="outline" className="hidden md:flex rounded-full">
                <Link href="/blog">View All Posts</Link>
              </Button>
            </div>

            <div className="grid md:grid-cols-2 gap-8">
              {post.relatedPosts.map((related) => (
                <Link key={related.slug} href={`/blog/${related.slug}`} className="group">
                  <Card className="h-full border-gray-200 dark:border-gray-800 dark:bg-gray-950 hover:border-blue-500 dark:hover:border-blue-500/50 transition-all duration-300 hover:shadow-2xl hover:-translate-y-1">
                    <CardHeader className="p-8">
                      <div className="flex justify-between items-start mb-4">
                        <div className="w-10 h-10 rounded-lg bg-blue-50 dark:bg-blue-900/30 flex items-center justify-center">
                          <ArrowRight className="w-5 h-5 text-blue-600 dark:text-blue-400 -rotate-45 group-hover:rotate-0 transition-transform duration-300" />
                        </div>
                      </div>
                      <CardTitle className="text-2xl text-gray-900 dark:text-white group-hover:text-blue-600 dark:group-hover:text-blue-400 transition-colors mb-3">
                        {related.title}
                      </CardTitle>
                      <CardDescription className="dark:text-gray-400 text-base leading-relaxed line-clamp-2">
                        {related.excerpt}
                      </CardDescription>
                    </CardHeader>
                  </Card>
                </Link>
              ))}
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}